import * as React from 'react';
import { styled } from '@mui/material/styles';
import Box from '@mui/material/Box';
import ButtonBase from '@mui/material/ButtonBase';
import Container from '@mui/material/Container';
import {Typography} from "@mui/material";
import c1 from "../../statics/c1.jpg"
import c2 from "../../statics/c2.jpg"
import c3 from "../../statics/c3.jpg"
import c4 from "../../statics/c4.png"
import c5 from "../../statics/c5.jpg"
import c6 from "../../statics/c6.jpg"
import {Link} from "react-router-dom"
import {accessories, cosmetic, fashion, pajamas, shoes, suit} from "../util/category";

const ImageBackdrop = styled('div')(({ theme }) => ({
  position: 'absolute',
  left: 0,
  right: 0,
  top: 0,
  bottom: 0,
  background: '#000',
  opacity: 0.5,
  transition: theme.transitions.create('opacity'),
}));

const ImageIconButton = styled(ButtonBase)(({ theme }) => ({
  position: 'relative',
  display: 'block',
  padding: 0,
  borderRadius: 0,
  height: '40vh',
  [theme.breakpoints.down('md')]: {
    width: '100% !important',
    height: 100,
  },
  '&:hover': {
    zIndex: 1,
  },
  '&:hover .imageBackdrop': {
    opacity: 0.15,
  },
  '&:hover .imageMarked': {
    opacity: 0,
  },
  '&:hover .imageTitle': {
    border: '4px solid currentColor',
  },
  '& .imageTitle': {
    position: 'relative',
    padding: `${theme.spacing(2)} ${theme.spacing(4)} 14px`,
  },
  '& .imageMarked': {
    height: 3,
    width: 18,
    background: theme.palette.common.white,
    position: 'absolute',
    bottom: -2,
    left: 'calc(50% - 9px)',
    transition: theme.transitions.create('opacity'),
  },
}));

const images = [
  {
    url: c1,
    title: fashion,
    width: '40%',
  },
  {
    url: c2,
    title: suit,
    width: '20%',
  },
  {
    url: c3,
    title: shoes,
    width: '40%',
  },
  {
    url: c4,
    title: accessories,
    width: '38%',
  },
  {
    url: c5,
    title: pajamas,
    width: '28%',
  },
  {
    url: c6,
    title: cosmetic,
    width: '34%',
  }
];

export default function ProductCategories() {
  return (
    <Container component="section" sx={{ mt: 4, mb: 4 }}>
      <Typography variant="h4" marked="center" align="center" component="h2">
        Categoría
      </Typography>
      <Box sx={{ mt: 4, display: 'flex', flexWrap: 'wrap' }}>
        {images.map((image) => (
            <ImageIconButton
              key={image.title}
              style={{
                width: image.width,
              }}
            >
              <Box
                sx={{
                  position: 'absolute',
                  left: 0,
                  right: 0,
                  top: 0,
                  bottom: 0,
                  backgroundSize: 'cover',
                  backgroundPosition: 'center 40%',
                  backgroundImage: `url(${image.url})`,
                }}
              />
              <ImageBackdrop className="imageBackdrop" />
              <Link to={'/shop'}>
                <Box
                  sx={{
                    position: 'absolute',
                    left: 0,
                    right: 0,
                    top: 0,
                    bottom: 0,
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    color: 'common.white',
                  }}
                >
                  <Typography
                    component="h3"
                    variant="h6"
                    color="inherit"
                    className="imageTitle"
                  >
                    {image.title}
                    <div className="imageMarked" />
                  </Typography>
                </Box>
              </Link>
            </ImageIconButton>
        ))}
      </Box>
    </Container>
  );
}
